import React, {
  FormEvent,
  LegacyRef,
  useEffect,
  useRef,
  useState,
} from "react";
import { Link, useParams, useHistory } from "react-router-dom";
import Editor from "jodit-react";
import Header from "../../../components/Header";
import Sidebar from "../../../components/Sidebar";
import Icon from "../../../utils/Icon";
import Input from "../../../utils/Input";
import Footer from "../../../components/Footer";
import firebase, { db } from "../../../config/Firebase";
import { useData } from "../../../context/DataContext";
import { ContactInterface } from "../../../types/types";
const EditContact = () => {
  const history = useHistory();
  const [name, setName] = useState("");
  const [link, setLink] = useState("");
  const [loading, setLoading] = useState(false);
  const editor = useRef() as React.MutableRefObject<Editor>;
  const [color, setColor] = useState("");
  const [svg, setSvg] = useState("");
  const { id }: { id: string } = useParams();
  const { contact = [] }: { contact?: ContactInterface[] } = useData();
  useEffect(() => {
    const singleOne = contact!.find((el) => el.id === id);
    if (singleOne) {
      const { name, link, color, svg } = singleOne;
      setName(name);
      setLink(link);
      setColor(color);
      setSvg(svg);
    }
  }, [contact, id]);
  const handleClick = () => {
    firebase
      .auth()
      .signOut()
      .then(() => history.push("/"))
      .catch((error) => {});
  };
  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    setLoading(true);
    db.collection("contact")
      .doc(id)
      .update({ name, link, color, svg })
      .then(() => {
        setLoading(false);
      });
  };
  const removeButtons: string[] = [
    "|",
    "bold",
    "strikethrough",
    "underline",
    "italic",
    "eraser",
    "|",
    "superscript",
    "subscript",
    "|",
    "ul",
    "ol",
    "|",
    "outdent",
    "indent",
    "|",
    "font",
    "fontsize",
    "brush",
    "paragraph",
    "|",
    "image",
    "file",
    "video",
    "table",
    "link",
    "|",
    "align",
    "undo",
    "redo",
    "\n",
    "selectall",
    "cut",
    "copy",
    "paste",
    "copyformat",
    "|",
    "hr",
    "symbol",
    "fullsize",
    "print",
    "preview",
    "find",
    "about",
  ];
  const config = { ...Jodit.defaultOptions, theme: "black", removeButtons };
  return (
    <div className="add_panel">
      <Header admin={true} />
      <Sidebar admin={true} />
      <div className="outer_container">
        <form
          onSubmit={handleSubmit}
          id="accesspanel"
          className="container contact_link_form adding_form"
          method="post"
        >
          {loading && (
            <div className="loader_success">
              <Icon loader={true} />
            </div>
          )}
          <h1 id="litheader" className="light_heading">
            ADD NEW PROJECT
          </h1>
          <div className="row">
            <Input
              onChange={(e) => setName(e.target.value)}
              value={name}
              type="text"
              id="name"
              placeholder="Name"
            />
            <Input
              onChange={(e) => setLink(e.target.value)}
              value={link}
              type="url"
              id="link"
              placeholder="Link to the Site"
            />
          </div>
          <div className="row">
            <div className="col-sm-6">
              <label htmlFor="details">SVG Icon:</label>
              <div className="btn_animate jodit_editor input_animate">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <div>
                  <Editor value={svg} ref={editor} config={config} />
                </div>
              </div>
            </div>
            <Input
              onChange={(e) => setColor(e.target.value)}
              value={color}
              type="text"
              id="color"
              placeholder="Color"
            />
          </div>
          <div className="center btn_wrapper">
            <button className="btn_animate browser-default">
              <span></span>
              <span></span>
              <span></span>
              <span></span>
              Add new project
              <span className="shift">›</span>
            </button>
            <Link to="/" className="btn_animate browser-default">
              <span></span>
              <span></span>
              <span></span>
              <span></span>
              Home
            </Link>
            <div
              className="btn_animate"
              style={{ display: `inline-block` }}
              onClick={handleClick}
            >
              <span></span>
              <span></span>
              <span></span>
              <span></span>
              Logout &nbsp;
              <span className="shift">›</span>
            </div>
          </div>
        </form>
        <Footer />
      </div>
    </div>
  );
};

export default EditContact;
